import React, { Component } from "react";
import axios from "axios";
import './Nba.css';

class Nba extends Component {


  constructor(props){
    super(props)
    this.state={
      playerName: null,
      playerStats: {}
    }
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.getPlayerId()
    console.log(this.state.playerName)
  }

  handlechange = (event) => {
    const replace =  event.target.value.split(" ").join("_");
    if (replace.length > 0) {
      this.setState({playerName: replace})
    } else {
      alert("ATTENTION! Le nom du joueur est invalide")
    }
  } 


  getPlayerId = () => {
    axios.get(`https://www.balldontlie.io/api/v1/players?search=${this.state.playerName}`)
    .then(async res => {
      //console.log(res.data.data)
      if (res.data.data[0] === undefined) {
        //alert("ATTENTION! Ce joueur n'a pas encore jouer")
      } else if (res.data.data.length > 1) {
        alert("Le nom est incomplet")
      }else{
        await this.getPlayerStats(res.data.data[0].id)
      }
    }).catch(err => {
      console.log(err)
    })
  }

  getPlayerStats = (playerId) => {
    axios.get(`https://www.balldontlie.io/api/v1/season_averages?season=2020&player_ids[]=${playerId}`)
    .then(async res => {
      console.log(res.data.data)
      this.setState({playerStats: res.data.data[0]})
    }).catch(err => {
      console.log(err)
    })
  }

  

  render(){
    return (
      <div className="App">
          <form onSubmit={this.handleSubmit}>
            <label>
              <input type="text" value={this.state.value} onChange={this.handlechange} placeholder="Le nom du joueur"/>
            </label>
            <input type="submit" value="Valider"/>
          </form>

          Matchs joués : {this.state.playerStats["games_played"]}
          <br />
          Moyenne de point par match : {this.state.playerStats["pts"]}
          <br />
          Moyenne de rebonds : {this.state.playerStats["reb"]}
          <br />
          Moyenne d'assistes : {this.state.playerStats["ast"]}
        </div>

        
    )
  }

}

export default Nba;
